import { words } from './Words';
import { sentences } from './Sentences';
import { shuffle } from '../helper/functionality';
const QuestionSource = { words, sentences };
export const getQuestions = () => {
    fetch('/projects/english-learner/source/words.json').then(res => {
        res.json().then(val => { QuestionSource.words = val; }).catch(err => { console.log(err); });
    }).catch(err => { console.log(err); });
    fetch('/projects/english-learner/source/sentences.json').then(res => {
        res.json().then(val => { QuestionSource.sentences = val; }).catch(err => { console.log(err); });
    }).catch(err => { console.log(err); });
};
export const getRandomQuestion = () => {
    const types = ['choosing', 'matching', 'sorting'];
    const type = types[Math.floor(Math.random() * types.length)];
    if (type === 'choosing') {
        const qList = [], count = 4;
        const variants = ['words', 'sentences'];
        const variant = variants[Math.floor(Math.random() * variants.length)];
        let q = QuestionSource[variant][Math.floor(Math.random() * QuestionSource[variant].length)];
        for (let i = 0; i < count; i++) {
            while (qList.includes(q)) {
                q = QuestionSource[variant][Math.floor(Math.random() * QuestionSource[variant].length)];
            }
            qList.push(q);
        }
        return { qList, q: Math.floor(Math.random() * count), type };
    } else if (type === 'matching') {
        const qList = [], count = 6;
        let q = QuestionSource['words'][Math.floor(Math.random() * QuestionSource['words'].length)];
        for (let i = 0; i < count; i++) {
            while (qList.includes(q)) {
                q = QuestionSource['words'][Math.floor(Math.random() * QuestionSource['words'].length)];
            }
            q.pair = false;
            qList.push({ ...q });
            q.pair = true;
            qList.push(q);
        }
        return { qList: shuffle(JSON.parse(JSON.stringify(qList))), type };
    } else {
        let question = QuestionSource['sentences'][Math.floor(Math.random() * QuestionSource['sentences'].length)];
        return { question, words: question.en.split(' '), options: shuffle(question.en.split(' ')), answer: [], selection: 0, type };
    }
}
export const findQuestion = (key = 'Walnut', type = 'words') => {
    const qList = [], count = 4;
    let question = QuestionSource[type].find(a => {
        if (qList.length < count) { qList.push(a); }
        return a.en === key;
    });
    if (!question) {
        question = { type, en: 'Not found', tr: 'Bulunamadı' };
    }
    const q = qList.length - 1;
    qList[q] = question;
    return { qList, q };
}