import { useRef, useState } from 'react';
import { Link } from 'react-router-dom';
import { getRandomQuestion } from '../services/Questions';
import { ReactComponent as Back } from '../assets/back-solid.svg';
import { ReactComponent as Home } from '../assets/home-solid.svg';
import { ReactComponent as Forward } from '../assets/forward-solid.svg';
import Choosing from './Question/Choosing';
import Matching from './Question/Matching';
import Sorting from './Question/Sorting';
import '../styles/Questions.css';

function Question({ onEmotionChange, onScoreChange }) {
    const history = useRef({ undo: [], redo: [] });
    const [question, setQuestion] = useState(getRandomQuestion());

    const handleClick = (value) => {
        onScoreChange.current(value);
        onEmotionChange.current(value);
        if (value) {
            setTimeout(() => { onEmotionChange.current(); }, 700);
            history.current.redo = [];
            if (history.current.undo.length > 50) {
                history.current.undo.shift();
            }
            history.current.undo.push(question);
            setTimeout(() => {
                const newQuestion = getRandomQuestion();
                setQuestion(newQuestion);
            }, 1000);
        }
    }

    const handleUndo = () => {
        if (history.current.undo.length > 0) {
            history.current.redo.push(question);
            setQuestion(history.current.undo.pop());
        }
    }

    const handleRedo = () => {
        if (history.current.redo.length > 0) {
            if (history.current.undo.length > 50) {
                history.current.undo.shift();
            }
            history.current.undo.push(question);
            setQuestion(history.current.redo.pop());
        }
    }

    return (
        <div className="question-detail">
            <button className="el-btn" onClick={handleUndo}><Back /></button>
            <Link to="/" className="el-btn"><Home /></Link>
            <button className="el-btn" onClick={handleRedo}><Forward /></button>
            {question.type === 'choosing' ?
                <Choosing question={question} onAnswer={handleClick} /> :
                question.type === 'matching' ?
                    <Matching question={question} onAnswer={handleClick} /> :
                    <Sorting question={question} onAnswer={handleClick} />
            }
        </div>
    );
}
export default Question;